import Khc from '../images/icons/redcom-team-04.svg';
import Mpm from '../images/icons/redcom-team-05.svg';
import Kfm from '../images/icons/redcom-team-06.svg';
import Wst from '../images/icons/redcom-team-07.svg';
import Prak from '../images/icons/redcom-team-08.svg';
import Sf from '../images/icons/redcom-team-09.svg';
import Struktur from '../images/icons/karriere-vorzuege-01.svg';
import Familie from '../images/icons/karriere-vorzuege-03.svg';
import Wege from '../images/icons/karriere-vorzuege-04.svg';
