import React from 'react';
import PropTypes from 'prop-types';
import { Link } from 'react-router-dom'
import classNames from 'classnames'

const JobStructure = ({name, description, icon, withSpacer}) => (
  <div className="card col-md-4 col-sm-6 vorzuege">
    <img src={icon} className='rounded-circle' />
    <div className="card-body">
      <h4 className={classNames("card-title", withSpacer ? 'withSpacing' : null)}>{name}</h4>
      <p className="card-text">{description}</p>
    </div>
  </div>
)

JobStructure.propTypes = {
  text: PropTypes.string.isRequired,
  withSpacer: PropTypes.bool
}
export default JobStructure



