import React, { Component } from 'react';
import { withRouter } from 'react-router';
import Slider from 'react-slick';
import Jumbotron from '../components/Jumbotron'
import { Link } from 'react-router-dom'
import Messages from '../locales/DE'
import CardChess from '../images/card-chess.png';
import CardTower from '../images/card-tower.png';
import CardPig from '../images/card-pig.png';
import ErloesmanagementPDF from '../files/REDCOM_Erlösmanagement.pdf';
import KostenLeistungsrechnungPDF from '../files/REDCOM_Kosten-Leistungsrechnung.pdf';
import MedizinstrategiePDF from '../files/REDCOM_Medizinstrategie.pdf';
import ItmGrafik from '../images/itm-grafik.png';
import HambuechenLogo from '../images/logos/hambuechen.svg';
import WaltherLogo from '../images/logos/walther.svg';

class Home extends Component {
  render() {
    const settings = {
      dots: true,
      infinite: true,
      speed: 500,
      slidesToShow: 1,
      slidesToScroll: 1,
      fade: true,
      swipe: false,
      autoplay: true,
      autoplaySpeed: 5000,
      lazyLoad: true
    }
    const settingsPartner = {
      dots: true,
      infinite: true,
      speed: 500,
      slidesToShow: 1,
      slidesToScroll: 1,
      fade: true,
      swipe: false,
      autoplay: true,
      autoplaySpeed: 10000,
      lazyLoad: true
    }
    return (
      <div className='startpageJumbo'>
        <Slider {...settings}>
          <div>
            <Jumbotron
              title={Messages.rm.title}
              lead={Messages.rm.quote}
              author={Messages.rm.author}
              buttonLabel={Messages.jumbotron.buttonLabel}
              target='/redcom/portfolio/erloesmanagement'
            />
          </div>
          <div>
            <Jumbotron
              title={Messages.klr.title}
              lead={Messages.klr.quote}
              author={Messages.klr.author}
              buttonLabel={Messages.jumbotron.buttonLabel}
              target='/redcom/portfolio/kosten-leistungsrechnung'
              additionalClassName='jumboPig'
            />
          </div>
          <div>
            <Jumbotron
              title={Messages.ms.title}
              lead={Messages.ms.quote}
              author={Messages.ms.author}
              buttonLabel={Messages.jumbotron.buttonLabel}
              target='/redcom/portfolio/medizinstrategie'
              additionalClassName='jumboChess'
            />
          </div>
        </Slider>
        <div className='content-gray'>
          <div className="container home">
            <h1>Unser Portfolio</h1>
            <div className="row">
              <div className="col-md-4">
                <Link className="card leistungen" to='/redcom/portfolio/erloesmanagement'>
                  <div className="card-img-tower" alt="Card image cap" />
                  <div className="card-body">
                    <h4 className="card-title text-uppercase">Erlösmanagement</h4>
                    <p className="card-text">Erlöse steigern und Know-how ausbauen</p>
                  </div>
                </Link>
                <a href href={ErloesmanagementPDF}>Download PDF Broschüre</a>
              </div>
              <div className="col-md-4">
                <Link className="card leistungen" to='/redcom/portfolio/kosten-leistungsrechnung'>
                  <div className="card-img-pig" alt="Card image cap" />
                  <div className="card-body">
                    <h4 className="card-title text-uppercase">Kosten-Leistungsrechnung</h4>
                    <p className="card-text">Einsparungspotenziale finden und Prozesse optimieren</p>
                  </div>
                </Link>
                <a href href={KostenLeistungsrechnungPDF}>Download PDF Broschüre</a>
              </div>
              <div className="col-md-4">
                <Link className="card leistungen" to='/redcom/portfolio/medizinstrategie'>
                  <div className="card-img-chess" alt="Card image cap" />
                  <div className="card-body">
                    <h4 className="card-title text-uppercase">Medizinstrategie</h4>
                    <p className="card-text">Effiziente Strategien entwickeln und Zukunft gestalten</p>
                  </div>
                </Link>
                <a href href={MedizinstrategiePDF}>Download PDF Broschüre</a>
              </div>
            </div>
          </div>
        </div>
        <div className="container home">
          <h1>Die REDCOM Group</h1>
          <p>Die REDCOM Group hat das Ziel Leistungserbringer im Gesundheitswesen erfolgreich zu machen.</p>
          <p>Mit unseren Dienstleistungs- und Beratungsprodukten adressieren wir die relevanten Erfolgsfaktoren Ihrer Einrichtung.</p>
          <p>Ob es um die transparente, entscheidungsrelevante Darstellung und Optimierung Ihrer Kosten- und Erlössituation, um die Verbesserung von Organisations- bzw. Prozessineffizienzen oder die gesamte strategische Ausrichtung Ihres Unternehmens geht - wir sind der richtige Partner und das seit Anbeginn des DRG-Zeitalters.</p>
          <p>Unsere Kundenreferenzen sowie unsere Partner bestätigen den gemeinsamen Erfolg.</p>
        </div>
        <div className='content-gray'>
          <div className="container home">
            <div className='partners'>
              <h1>Unsere Partner</h1>
              <Slider {...settingsPartner}>
                <div>
                  <h2>Integriertes Turn-Around Management (ITM)</h2>
                  <p className='text-center'>Ab dem 1. Tag für Ihren Erfolg – ein integrierter Ansatz der Conzima GmbH, Kienbaum und der REDCOM Group.</p>
                  <div className="row justify-content-md-center">
                    <div className="card col-md-6">
                      <div className="card-body">
                        <img src={ItmGrafik} className='img-fluid' />
                      </div>
                    </div>
                  </div>
                </div>
                <div>
                  <h2>Forderungsmanagement</h2>
                  <p className='text-center card-padding'>Medizin-juristische Beratung auf höchstem Niveau in Kooperation mit den Kanzleien Dr. Ulrich Hambüchen sowie Alexander Walther.</p>
                  <div className="row justify-content-md-center">
                    <div className="card col-md-4 partner">
                      <div className='card-logo hambuechen'><img src={HambuechenLogo} className='img-fluid' /></div>
                      <div className="card-body">
                        <h4 className="card-title">Kanzlei Dr. Ulrich  Hambüchen</h4>
                        <p className="card-text">Dr. Hambüchen war jahrelang Vorsitzender des des für das Leistungserbringerrecht der GKV zuständigen 5. Senats des BSG und ist als Experte auf dem Gebiet des Forderungsmanagements besonders ausgewiesen.</p>
                      </div>
                    </div>
                    <div className="card col-md-4 partner">
                      <div className='card-logo walther'><img src={WaltherLogo} className='img-fluid' /></div>
                      <div className="card-body">
                        <h4 className="card-title">Kanzlei Alexander Walther</h4>
                        <p className="card-text">Die Rechtsanwaltskanzlei Walther ist eine auf das Medizin- und Krankenhausrecht spezialisierte Rechtsanwaltskanzlei mit Sitz in Mannheim und ist ein langjähriger Partner der REDCOM Group im Bereich Forderungsmanagement </p>
                      </div>
                    </div>
                  </div>
                </div>
              </Slider>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default withRouter(Home);



