import React from 'react';
import PropTypes from 'prop-types';

const descriptions = {
  'Kodierfachkräfte': 'Unsere Kodierfachkräfte brillieren mit ihrer langjährigen klinischen Praxiserfahrung im Bereich der Aktenanalyse und mit ihrem Blick für Optimierungsbedarf - stets unter der Prämisse des “Rightcoding”. Neben einem pflegerischen Einstieg bieten eine Kodierausbildung sowie fundierte Kenntnisse des DRG-Systems und der deutschen Kodierrichtlinien einen immensen Vorteil. Kodierfachkräfte führen für unsere Kunden die Primärkodierung der Patientenakten durch oder bearbeiten bereits verschlüsselte Akten im Zuge einer Kodierrevision. Bei Bedarf bereiten sie MDK-Besprechungen vor und begleiten das Krankenhaus bei einem MDK-Prüfverfahren.',
  'MDK Manager': 'Ebenfalls für uns im Einsatz sind unsere MDK Manager. Sie besitzen eine Begabung im Umgang mit den verschiedenen Interessensgruppen im Gesundheitswesen. Dabei kommen ihnen eine abgeschlossene medizinische oder kaufmännische Ausbildung im Gesundheitswesen sowie umfangreiche Kenntnisse des DRG-Abrechnungssystems zu Gute. Bei unseren Kunden vor Ort übernehmen sie die Bearbeitung der schriftlichen MDK-Anfragen seitens der Krankenkassen, prüfen diese und sprechen je nach Fall eine Klageempfehlung für den Kunden aus.',
  '(Nicht-) Ärztliche Medizincontroller': 'Bei uns finden Sie auch (Nicht-) Ärztliche Medizincontroller mit Gesamtprozesserfahrung, Detailkenntnis und Führungskompetenzen. Sie interessieren sich nicht nur für Fragen des DRG-Systems, der Kodierung und Abrechnungsprüfung, sondern insbesondere auch für die Optimierung medizinischer Prozesse unter Berücksichtigung von Kennzahlen des Qualitätsmanagements. Kenntnisse über die Gesundheitsgesetzgebung sowie die Fähigkeit zur Organisation und Konzeptentwicklung spielen hierbei ebenso eine große Rolle.',
  'Krankenhauscontroller': 'Krankenhauscontroller mit Berater-Gen sind rar. Bei uns arbeiten Profis mit Kosten- und Leistungsdaten eines Krankenhauses – sie kennen alle Details des Datensatzes nach § 21 KHEntgG und können diese unter Zuhilfenahme eigener REDCOM-Tools plausibilisieren und in Reportings überführen. Ein abgeschlossenes Studium oder eine vergleichbare Ausbildung mit Schwerpunkt Controlling bevorzugt im Gesundheitswesen dienen hier als Grundvoraussetzung. Durch ihre langjährigen Erfahrungen besitzen unsere Controller ausgeprägte Kenntnisse im Bereich der Krankenhausstrukturen und Krankenhausabläufe.',
  'Mediziner und Pflegemanager': 'Unsere Mediziner und Pflegemanager sind absolute Experten in Sachen Strategie- und Organisationsberatung. Mit ihrem Know-how erkennen sie bei unseren Kunden Handlungsnotwendigkeiten in den Bereichen Medizinstrategie, Prozessoptimierung und Projektmanagement. Unsere Berater erarbeiten auf den jeweiligen Kunden maßgeschneiderte Konzeptionen, begleiten den gesamten Umsetzungsprozess mithilfe von Coachings und Moderationen und schließen die Projekterfolge mit Verbesserungsvorschlägen und Management-Controllings ab. Dabei kommt ihnen ihre langjährige Expertise im Krankenhausbereich zu Gute.',
  'Kaufmännische Angestellte': 'Neben unseren Fachbereichen tragen natürlich auch unsere kaufmännischen Angestellten aus den Abteilungen Buchhaltung, Vertrieb & Marketing, Business Development, EDV oder Human Resources zu unserem Erfolg bei. Unsere Mitarbeiter zeichnen sich durch selbstständige, strukturierte und pragmatische Arbeitsweise sowie ihre Fähigkeit zum bereichsübergreifenden Arbeiten aus. Dabei spielen eine ausgeprägte Kommunikationsstärke sowie ein verbindliches und serviceorientiertes Auftreten gegenüber unseren Mitarbeitern, Interessenten und Kunden eine große Rolle. Kenntnisse über das deutsche Gesundheitssystem sind hier ebenso von Vorteil.',
  'Werkstudenten': 'Werkstudenten sammeln bei uns wertvolle Praxis-  bzw. Projekterfahrung und werden bei uns als vollwertige Mitarbeiter integriert. Sie unterstützen unsere Berater sowohl im Bereich Krankenhauscontrolling als auch bei der Strategie- und Organisationsberatung. Mit unserer Unterstützung bauen sie ihre Karriere im Gesundheitswesen aus. Durch die Kooperation mit verschiedenen Hochschulen und Universitäten betreuen wir unsere Werkstudenten nach gemeinsamer Themen-Absprache auch gerne in puncto Bachelor- bzw. Masterarbeit. Idealerweise bieten wir den Studenten nach ihrem Studium attraktive Weiterentwicklungsmöglichkeiten mit Aussicht auf einen Direkteinstieg.',
  'Praktikanten': 'Im Rahmen ihres Studiums der Fachrichtungen Gesundheits-, Pflege- oder Krankenhausmanagement können Studenten in einem freiwilligen- oder Pflichtpraktikum ihre beruflichen Möglichkeiten ausloten und so wertvolle Erfahrungen in der Gesundheitsbranche sammeln. Wie unsere Werkstudenten auch werden sie dabei als vollwertige Mitarbeiter betrachtet, die wir in unserem Arbeitsalltag integrieren. Auch hier bieten wir gerne attraktive Weiterentwicklungsmöglichkeiten wie beispielsweise eine Werkstudententätigkeit bis hin zu einem Direkteinstieg.'
}
const JobModal = ({name}) => (
  <div className="modal fade" id="exampleModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
    <div className="modal-dialog" role="document">
      <div className="modal-content">
        <div className="modal-header">
          <h5 className="modal-title" id="exampleModalLabel">{name}</h5>
          <button type="button" className="close" data-dismiss="modal" aria-label="Close">
            <span aria-hidden="true">&times;</span>
          </button>
        </div>
        <div className="modal-body">{descriptions[name]}</div>
      </div>
    </div>
  </div>
)

JobModal.propTypes = {
  name: PropTypes.string.isRequired
}
export default JobModal



