import React from 'react';
import PropTypes from 'prop-types';
import { Link } from 'react-router-dom'

const Quote = ({text}) => (
  <div className="quote">
    <div className="container">
      {text}
    </div>
  </div>
)

Quote.propTypes = {
  text: PropTypes.string.isRequired
}
export default Quote



