import React from 'react';
import { BrowserRouter as Router, Route } from 'react-router-dom'
import {
  Home,
  Erloesmanagement,
  KostenLeistungsrechnung,
  Medizinstrategie,
  Karriere,
  Kontakt,
  Datenschutz,
  Impressum
} from './pages'
import Navigation from './components/Navigation'
import Footer from './components/Footer'
import './App.css';

const App = () => (
  <Router>
    <div>
      <Navigation />
      <Route exact path="/redcom" component={Home} />
      <Route path="/redcom/portfolio/erloesmanagement" component={Erloesmanagement} />
      <Route path="/redcom/portfolio/kosten-leistungsrechnung" component={KostenLeistungsrechnung} />
      <Route path="/redcom/portfolio/medizinstrategie" component={Medizinstrategie} />
      <Route path="/redcom/karriere" component={Karriere} />
      <Route path="/redcom/kontakt" component={Kontakt} />
      <Route path="/redcom/datenschutz" component={Datenschutz} />
      <Route path="/redcom/impressum" component={Impressum} />
      <Footer />
    </div>
  </Router>
)
export default App



