import React, { Component } from 'react';
import classNames from 'classnames'
import { Link } from 'react-router-dom'
import { matchPath } from 'react-router'
import NavigationLink from './NavigationLink'
import NavigationDropdown from './NavigationDropdown'
import logo from '../images/redcom-logo.svg';

class Navigation extends Component {
  render () {
    return (
      <div>
      <nav className="navbar navbar-expand-lg navbar-light">
        <div className='container'>
          <div className="collapse navbar-collapse" id="navbarSupportedContent">
            <ul className="navbar-nav mr-auto">
              <NavigationLink name='Über uns' target='/redcom' />
              <NavigationDropdown name='Erlösmanagement' target='/redcom/erloesmanagement' />
              <NavigationLink name='Karriere' target='/redcom/karriere' />
            </ul>
          </div>
          <Link className="navbar-brand" to="/redcom">
            <img src={logo} alt="" />
          </Link>
          <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
            <span className="navbar-toggler-icon"></span>
          </button>
        </div>
      </nav>
    </div>
    )
  }
}
export default Navigation



