import React, { Component } from 'react';
import { withRouter } from 'react-router';
import { NavLink } from 'react-router-dom'
import Messages from '../locales/DE';
import Datenplausibilisierung from '../images/kosten-leistungsrechnung/datenplausibilisierung.svg';
import Inek from '../images/kosten-leistungsrechnung/inek.svg';
import Kosten from '../images/kosten-leistungsrechnung/kosten.svg';
import Reporting from '../images/kosten-leistungsrechnung/reporting.svg';
import Benchmark from '../images/kosten-leistungsrechnung/benchmark.svg';
import Quintessenz from '../components/Quintessenz';

class KostenLeistungsrechnung extends Component {
  render() {
    return (
      <div>
      <div className="karriereJumbo1 jumbotron jumbotron-fluid">
        <h1 className="text-center text-uppercase withBackground">Kosten-Leistungsrechnung</h1>
      </div>
      <div className="container erloesmanagement">
        <div className="row prozesse datenplausibilisierung">
          <div className="card col-lg-6">
            <div className="card-body text">
              <h1>Datenplausibilisierung</h1>
              <p>Vollständigkeit und inhaltliche Validität von Kosten-, Leistungs- und Strukturdaten entscheiden über die Qualität der daraus abgeleiteten Managementinformationen.</p>
              <p>Datenlücken, Fehlerfassung und verspätete Eintragungen in der Dokumentation führen zwangsläufig zu Fehlinformationen und ggf. falschen Entscheidungen.</p>
              <p>Eine werthaltige Datenplausibilisierung muss aus unserer Sicht unter allen Umständen die medizinische, klassifikatorische und ökonomische Perspektive enthalten.</p>
              <p>Dieser interdependente Ansatz und unser umfangreiches Benchmark führen zur nachhaltigen Steigerung der Datenqualität und der Aussagekraft steuerungsrelevanter Informationen.</p>
            </div>
          </div>
          <div className="card col-lg-6">
            <div className="card-body grafik">
              <img src={Datenplausibilisierung} />
            </div>
          </div>
        </div>
      </div>
      <Quintessenz>
        <ul className="list-group">
          <li className="list-group-item">Übergreifende Datenanalyse und Plausibilisierung aller Leistungs- und Kostenbereiche</li>
          <li className="list-group-item">Medizin-ökonomischer Ansatz zur Datenplausibilisierung</li>
          <li className="list-group-item">Unterstützung durch klinische Verteilungsmodelle</li>
        </ul>
      </Quintessenz>

      <div className="container erloesmanagement">
        <h1>Kostenträgerrechnung</h1>
        <p>Die Kostenträgerrechnung als Fallkostenkalkulation ist das einzige Controllinginstrument, welches konkrete Aussagen über die ökonomische Vorteilhaftigkeit einzelner Behandlungsfälle oder medizinisch homogener Fallgruppen zulässt.</p>
        <p>Die prozessorientierte Darstellung von Kosten und Erlösen anhand des Leistungsdatums lässt Aussagen über die Effizienz von medizinischen Abläufen zu und ermöglicht die steuerungsrelevante Diskussion mit allen medizinischen Berufsgruppen.</p>
        <img src={Kosten} className='imgBeratung' />
      </div>
      <Quintessenz>
        <ul className="list-group">
          <li className="list-group-item">Langjährige Expertise im Aufbau und der Durchführung der Kostenträgerrechnung im Krankenhaus</li>
          <li className="list-group-item">Adaption des InEK-Kalkulationsschemas für Ihre Vergleichbarkeit</li>
          <li className="list-group-item">Einbindung der stationären und ambulanten Bereiche</li>
          <li className="list-group-item">Begleitende Beratung und Schulung durch unsere Experten</li>
          <li className="list-group-item">Eigenentwickeltes Kalkulations-Tool für größtmögliche Flexibilität</li>
          <li className="list-group-item">Vollkommene Transparenz in allen Schritten</li>
        </ul>
      </Quintessenz>

      <div className="container erloesmanagement">
        <div className="row prozesse">
          <div className="card col-lg-6">
            <div className="card-body text">
              <h1>InEK-Kalkulation</h1>
              <p>Mit unserem ganzheitlichen Ansatz betreuen wir Sie von A bis Z.</p>
              <p>Durch unsere 100%ige Erfolgsquote seit der ersten Kalkulation für unsere Kunden sichern Sie sich eine attraktive Rückvergütung mit der Teilnahme an der InEK-Kalkulation.</p>
            </div>
          </div>
          <div className="card col-lg-6">
            <div className="card-body grafik">
              <img src={Inek} />
            </div>
          </div>
        </div>
      </div>
      <Quintessenz>
        <ul className="list-group">
          <li className="list-group-item">Jahrelange Erfahrung in der erfolgreichen Durchführung der InEK-Kalkulation für unsere Kunden</li>
          <li className="list-group-item">Übernahme des gesamten Kalkulationsprozesses von der Datenaufbereitung bis hin zu den abschließenden Erläuterungen für das InEK-Institut</li>
          <li className="list-group-item">Betreuung durch Experten aus Ökonomie und Medizin</li>
          <li className="list-group-item">Ausbau der Ergebnisse zu einem steuerungsrelevanten Berichtswesen</li>
          <li className="list-group-item">Aktiver Beitrag zur Entwicklung des DRG-Systems</li>
        </ul>
      </Quintessenz>

      <div className="container erloesmanagement">
        <div className="row prozesse">
          <div className="card col-lg-6">
            <div className="card-body text">
              <h1>Berichtswesen</h1>
              <p>Reportings sind kein Selbstzweck, sondern sollen Entscheider in die Lage versetzen, über ihren Verantwortungsbereich hinaus erfolgreicher zu führen und zu gestalten.</p>
              <p>Schnell verständlich, übersichtlich und umsetzungsorientiert konzipieren wir Ihr Berichtswesen und geben Ihnen konkrete Hinweise, Analysen und praktische Empfehlungen an die Hand.</p>
            </div>
          </div>
          <div className="card col-lg-6">
            <div className="card-body grafik">
              <img src={Reporting} />
            </div>
          </div>
        </div>
      </div>
      <Quintessenz>
        <ul className="list-group">
          <li className="list-group-item">Entscheidungsorientierung als Prämisse</li>
          <li className="list-group-item">Flexible und interaktive Analysemöglichkeiten</li>
        </ul>
      </Quintessenz>

      <div className="container erloesmanagement">
        <div className="row prozesse">
          <div className="card col-lg-6">
            <div className="card-body text">
              <h1>Benchmark</h1>
              <p>Benchmarking macht nur Sinn, wenn das Vergleichsobjekt passt und der Weg zur Verbesserung klar erkennbar ist.</p>
              <p>Warum vergleichen Sie sich nicht mit sich selbst?</p>
              <p>Nicht ganz - vielmehr mit Ihrem „Alter Ego“, virtuell nachgebaut auf der grünen Wiese mit unseren Best-Practice-Daten.</p>
              <p>Abweichungsanalyse, Maßnahmendefinition und Umsetzungsbegleitung liefern wir gerne mit.</p>
            </div>
          </div>
          <div className="card col-lg-6">
            <div className="card-body grafik">
              <img src={Benchmark} />
            </div>
          </div>
        </div>
      </div>
      <Quintessenz>
        <ul className="list-group">
          <li className="list-group-item">Konzeptionierung von individuellen Benchmark-Projekten</li>
          <li className="list-group-item">Simulation von Handlungsoptionen zur Unterstützung Ihrer Entscheidungen</li>
          <li className="list-group-item">Weiterführende Begleitung in Ihrem Changemanagement-Prozess</li>
          <li className="list-group-item">Projektmanagement durch erfahrene Mediziner und Ökonomen</li>
        </ul>
      </Quintessenz>
      </div>
    );
  }
}

export default withRouter(KostenLeistungsrechnung);



