import React from 'react';
import PropTypes from 'prop-types';
import { NavLink } from 'react-router-dom'

const NavigationDropdown = ({name, target}) => (
  <li className="nav-item dropdown">
    <a className="nav-link dropdown-toggle" href="#" id="navbarDropdown" role="button" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
      Portfolio
    </a>
    <div className="dropdown-menu" aria-labelledby="navbarDropdown">
      <NavLink className="dropdown-item" activeClassName="active" exact to='/redcom/portfolio/erloesmanagement'>Erlösmanagement</NavLink>
      <NavLink className="dropdown-item" activeClassName="active" exact to='/redcom/portfolio/kosten-leistungsrechnung'>Kosten-Leistungsrechnung</NavLink>
      <NavLink className="dropdown-item" activeClassName="active" exact to='/redcom/portfolio/medizinstrategie'>Medizinstrategie</NavLink>
    </div>
  </li>
)
export default NavigationDropdown



