import React, { Component } from 'react';
import { withRouter } from 'react-router';
import { NavLink } from 'react-router-dom'
import Messages from '../locales/DE'
import Quintessenz from '../components/Quintessenz';
import KnowHow from '../images/medizinstrategie/know-how.svg';
import Analyse1 from '../images/medizinstrategie-analyse1.svg';
import Analyse2 from '../images/medizinstrategie-analyse2.svg';
import Portfolio from '../images/medizinstrategie/portfolio.svg';
import Prozesse from '../images/medizinstrategie/prozesse.svg';
import Steuerung from '../images/medizinstrategie/steuerung.svg';
import Tagesablauf from '../images/medizinstrategie/tagesablauf.svg';
import Cosima from '../images/cosima.png';

class Medizinstrategie extends Component {
  render() {
    return (
      <div>
      <div className="karriereJumbo1 jumbotron jumbotron-fluid">
        <h1 className="text-center text-uppercase withBackground">Medizinstrategie</h1>
      </div>
      <div className="container erloesmanagement">
        <div className="row knowhow">
          <div className="card col-lg-6">
            <div className="card-body text">
              <h1>Unser Know-how</h1>
              <p>
                Mit unserer Branchen- und Fachkompetenz gehören wir zu den Spitzenadressen für strategische Klinikberatung.
                <br />
                Unser Team besteht aus erfahrenen Medizinern, Ökonomen und Pflegekräften und verfolgt den Beratungsansatz der Zieldefinition, Analyse, Konzeption und Umsetzungsbegleitung.
              </p>
              <p><strong>TOP 10-PLATZIERUNG IN DER TRENDRESEARCH-STUDIE "BERATUNGSLEISTUNGEN FÜR DAS GESUNDHEITSWESEN"</strong></p>
            </div>
          </div>
          <div className="card col-lg-6">
            <div className="card-body grafik">
              <img src={KnowHow} />
            </div>
          </div>
        </div>
      </div>
      <div className="container erloesmanagement">
        <div className="row prozesse">
          <div className="card col-lg-6">
            <div className="card-body text">
              <h1>Medizinisches Leistungsportfolio</h1>
              <p>Maßgeschneiderte Zukunftsstrategien sind im Wettbewerb entscheidend für den Erfolg. Wir helfen Ihnen die Grundlagen dafür zu legen und begleiten Sie auf dem Weg der spezifischen Ausrichtung Ihres medizinischen Leistungsangebotes.</p>
              <p>Erfolg beginnt mit der Ausrichtung des Leistungsangebotes am Markt. Wir unterstützen Sie dabei mit Analysen, der Ableitung von wesentlichen Veränderungspotenzialen sowie der Planung und Umsetzung von abgestimmten Maßnahmen.</p>
            </div>
          </div>
          <div className="card col-lg-6">
            <div className="card-body grafik">
              <img src={Portfolio} />
            </div>
          </div>
        </div>
      </div>
      <Quintessenz>
        <ul className="list-group">
          <li className="list-group-item">Profunde Projektvorbereitung und professionelles Projektmanagement</li>
          <li className="list-group-item">Wir unterstützen Sie, die richtigen Ziele und Maßnahmen zu definieren</li>
          <li className="list-group-item">Veränderung funktioniert nur unter Einbeziehung aller beteiligter Berufsgruppen. Wir integrieren Ihre Leistungsträger in unsere Projekte.</li>
          <li className="list-group-item">Wir sorgen von Beginn an durch Kommunikation für Akzeptanz, Motivation und Mitwirkung</li>
        </ul>
      </Quintessenz>
      <div className="container erloesmanagement">
        <h1>Effiziente Prozesse</h1>
        <p>Medizinische Prozesse befinden sich im Spannungsfeld von ökonomischer Realität und Behandlungsqualität für Ihre Patienten. Nur funktionierende, effiziente und transparente Prozesse können wirtschaftlich und medizinisch zielführend sein.</p>
        <p>Wir helfen Ihnen, unter starker Einbindung und Mitwirkung aller Ihrer Fachbereiche sowie Leistungsträger, die medizinischen Prozesse Ihres Unternehmens so zu gestalten, dass Sie langfristig erfolgreich sind.</p>
        <img src={Prozesse} className='imgBeratung' />
      </div>
      <Quintessenz>
        <ul className="list-group">
          <li className="list-group-item">Optimierung der ambulanten Bereiche sowie aller medizinischen Prozesse, von der Aufnahme bis zur Entlassung</li>
          <li className="list-group-item">Starke Einbindung aller Berufsgruppen und Leistungsträger in den Veränderungsprozess</li>
          <li className="list-group-item">Definition von gemeinsamen Zielen der Prozessverbesserung und Kennzahlen zur Messung der Erreichung</li>
          <li className="list-group-item">In unseren Projekten steht die Umsetzungsorientierung aller Maßnahmen im Fokus</li>
        </ul>
      </Quintessenz>
      <div className="container erloesmanagement">
        <h1>Personal und bauliche Ressourcen</h1>
        <p>Medizinischer und wirtschaftlicher Erfolg bedarf effizienter Prozesse und Organisationsstrukturen. Hierfür müssen vorhandene und zukünftige Ressourcen bestmöglich eingesetzt sowie genutzt werden.</p>
      </div>
      <Quintessenz title='Um dies sicherzustellen, unterstützen wir Sie bei Ihrer'>
        <ul className="list-group">
          <li className="list-group-item">Personaleinsatzplanung sowie weiterhin bei der Entwicklung und Umsetzung effizienter Schichtdienstmodelle</li>
          <li className="list-group-item">Zielgerichteten Ausgestaltung Ihres Raum- und Funktionsprogrammes</li>
        </ul>
      </Quintessenz>
      <div className="container medizinstrategie erloesmanagement">
        <img src={Tagesablauf} className='imgTagesablauf' />
        <h1>Zukünftige Strukturen und Steuerung</h1>
        <p>Strukturänderungen sind immer mit Unsicherheiten verbunden. Die fundierte Simulation von Veränderungseffekten schafft Transparenz, klares Wissen über die Vorteilhaftigkeit geplanter Maßnahmen und ermöglicht deren effiziente Steuerung.</p>
        <img src={Steuerung} className='imgSteuerung' />
        <p>COSIMA - Die Kosten- und Erlössimulation medizinischer Abteilungen als kompakte, softwaregestützte Expertenanalyse schafft schnelle Transparenz und gibt Ihnen Sicherheit in der Bewertung geplanter Veränderungen.</p>
        <img src={Cosima} className="img-fluid" alt="Responsive image" />
        <div id="accordion" className='row' role="tablist">
          <div className='col-lg-6'>
            <div className="card">
              <div className="card-header" role="tab" id="headingOne">
                <h5 className="mb-0">
                  <a data-toggle="collapse" href="#collapseOne" aria-controls="collapseOne">
                    Personaleinsatzoptimierung
                  </a>
                </h5>
              </div>
              <div id="collapseOne" className="collapse" role="tabpanel" aria-labelledby="headingOne" data-parent="#accordion">
                <div className="card-body">Die Ermittlung von Auffälligkeiten und die Bewertung des Schichtdienstmodells erlaubt die Definition von Handlungsmaßnahmen.</div>
              </div>
            </div>
          </div>
          <div className='col-lg-6'>
            <div className="card">
              <div className="card-header" role="tab" id="headingThree">
                <h5 className="mb-0">
                  <a className="collapsed" data-toggle="collapse" href="#collapseThree" aria-expanded="false" aria-controls="collapseThree">
                    Prozessoptimierung
                  </a>
                </h5>
              </div>
              <div id="collapseThree" className="collapse" role="tabpanel" aria-labelledby="headingThree" data-parent="#accordion">
                <div className="card-body">
                  Die Methode des Lean Management schließt eine Erhöhung der Effizienz, Verkürzung der Durchlaufzeiten sowie Glättung von Auslastungsschwankungen ein.
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      </div>
    );
  }
}

export default withRouter(Medizinstrategie);



