import React from 'react';
import PropTypes from 'prop-types';
import { Link } from 'react-router-dom'
import classNames from 'classnames'
const Jumbotron = ({title, lead, author, target, buttonLabel, additionalClassName}) => {
  return (
    <div className={classNames(additionalClassName, "jumbotron jumbotron-fluid")}>
      <div className="container">
        <h1 className="text-center text-uppercase">{title}</h1>
        <p className="text-center">{lead}</p>
        <p className="text-center">{author}</p>
        <Link className="btn btn-danger" to={target}>{buttonLabel}</Link>
      </div>
    </div>
  )
}

Jumbotron.propTypes = {
  title: PropTypes.string.isRequired,
  lead: PropTypes.string.isRequired,
  buttonLabel: PropTypes.string.isRequired,
  target: PropTypes.string.isRequired
}
export default Jumbotron



